
body{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.container{
  height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.cards{
  display: flex;
 justify-content: center;

}
.card{
  background: rgb(132, 246, 132);
  width: 300px;
  margin: 0 20px;
  height: 400px;
  text-align: center;
  box-shadow: 5px 5px 10px rgb(182, 182, 182); 
}
.bgimage{
  background-image: url('https://images.newscientist.com/wp-content/uploads/2021/02/16162929/naturepl_01583510_web.jpg?width=600');
  width: 100%;
  height: 100px;
  background-size: cover;
  background-position: center;
}
.profile{
  background-image: url('https://www.heart.org/-/media/images/healthy-living/healthy-lifestyle/man_relaxing_in_nature.jpg');
  width: 100px;
  height: 100px;
  background-size: cover;
  background-position: center;
  border-radius: 50%;
  margin: -50px auto 0 auto;
}
p{
  width: 80%;
  margin: 0 auto;
}
button{
  margin-top: 40px;
  border: none;
  padding: 10px 20px;
  width: 80%;
  box-sizing: border-box;
  font-size: 16px;
  font-weight: bold;
  background: rgb(37, 167, 37);
  color: white;
  cursor: pointer;
  transition: 0.5s;
  outline: none;
}
button:hover{
  color: rgb(37, 167, 37);
  background: white;
}
